<template lang="">
    <div >
        <div class="divVagaFavorita ">
            <button class="btn btn-dark" type="button" data-bs-toggle="offcanvas" 
            data-bs-target="#offcanvasRight" aria-controls="offcanvasRight">Vagas Favoritadas</button>
        </div>

        <div class="offcanvas offcanvas-end" tabindex="-1" id="offcanvasRight" aria-labelledby="offcanvasRightLabel">
            <div class="offcanvas-header">
                <h5 class="offcanvas-title" id="offcanvasRightLabel">Vagas Favoritadas</h5>
                <button type="button" class="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
            </div>
            <div class="offcanvas-body">
                <ul class="list-group">
                    <li class="list-group-item" v-for="(vaga, index) in vagas" :key="index">{{vaga}}</li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'VagasFavoritas',
    data:()=>({
        vagas: []
    }),
    mounted(){
        this.emitter.on('favoritarVaga', (titulo)=>{
            this.vagas.push(titulo)
        })
        this.emitter.on('desFavoritarVaga', (titulo)=>{
            let indiceArray = this.vagas.indexOf(titulo)
            if(indiceArray !== -1){this.vagas.splice(indiceArray, 1)}
        })
    }
}
</script>
<style scoped>
.divVagaFavorita{
    position: absolute; z-index: 1; top: 10px; right:10px;
}

@media only screen and (max-width: 600px) {
 .divVagaFavorita button{
    font-size: 0.8em;
   

}
}
</style>